.form {
  max-width: 500px;
  border: 2px solid #ddd;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin: 50px auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

label {
  flex-direction: column;
  margin-bottom: 15px;
  display: flex;
}

input[type="number"] {
  width: 100%;
  border: 2px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button[type="submit"]:hover {
  background-color: #0062cc;
}

button[type="submit"]:disabled {
  color: #777;
  cursor: not-allowed;
  background-color: #ddd;
}

button[type="submit"]:disabled:hover {
  background-color: #ddd;
}

button[type="submit"]:active {
  transform: translateY(1px);
}

